<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Tambah Soal Kedinasan</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <div id="editor-container"></div>
        <ion-item>
          <ion-label>Pilihan A</ion-label>
          <ion-input v-model="optionA" placeholder="Masukkan pilihan A"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan B</ion-label>
          <ion-input v-model="optionB" placeholder="Masukkan pilihan B"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan C</ion-label>
          <ion-input v-model="optionC" placeholder="Masukkan pilihan C"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan D</ion-label>
          <ion-input v-model="optionD" placeholder="Masukkan pilihan D"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Pilihan E</ion-label>
          <ion-input v-model="optionE" placeholder="Masukkan pilihan E"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Jawaban Benar</ion-label>
          <ion-select v-model="correctAnswer" placeholder="Pilih Jawaban Benar">
            <ion-select-option value="A">A</ion-select-option>
            <ion-select-option value="B">B</ion-select-option>
            <ion-select-option value="C">C</ion-select-option>
            <ion-select-option value="D">D</ion-select-option>
            <ion-select-option value="E">E</ion-select-option>
          </ion-select>
        </ion-item>

        <!-- Tambahan Kolom Pembahasan -->
        <ion-item>
          <ion-label>Pembahasan Soal</ion-label>
        </ion-item>
        <div id="editor-explanation-container"></div>

        <ion-button style="color: white" expand="block" @click="addQuestion">Tambah Soal</ion-button>
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Soal</h2>
              <ion-list>
                <ion-item>
                  <ion-label>
                    <div class="table-container">
                      <table>
                        <thead>
                          <tr>
                            <th>No</th>
                            <th>Soal</th>
                            <th>Pilihan A</th>
                            <th>Pilihan B</th>
                            <th>Pilihan C</th>
                            <th>Pilihan D</th>
                            <th>Pilihan E</th>
                            <th>Jawaban Benar</th>
                            <th>Pembahasan</th>
                            <th>Aksi</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="question in questions" :key="question.id">
                            <td>{{ index + 1 }}</td>
                            <td v-html="question.question"></td>
                            <td>{{ question.options.A }}</td>
                            <td>{{ question.options.B }}</td>
                            <td>{{ question.options.C }}</td>
                            <td>{{ question.options.D }}</td>
                            <td>{{ question.options.E }}</td>
                            <td>{{ question.correctAnswer }}</td>
                            <td v-html="question.explanation"></td>
                            <td>
                              <ion-button @click="editQuestion(question)" color="warning">Edit</ion-button>
                              <ion-button @click="deleteQuestion(question.id)" color="danger">Hapus</ion-button>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </ion-label>
                </ion-item>
              </ion-list>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>
      <ion-toast :is-open="toast.isOpen" :message="toast.message" :duration="toast.duration" :color="toast.color" @did-dismiss="() => (toast.isOpen = false)"></ion-toast>
      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal" class="custom-modal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Soal</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal" >Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <div id="editor-edit-container"></div>
          <ion-item>
            <ion-label>Pilihan A</ion-label>
            <ion-input v-model="editOptionA" placeholder="Masukkan pilihan A"></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan B</ion-label>
            <ion-input v-model="editOptionB" placeholder="Masukkan pilihan B"></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan C</ion-label>
            <ion-input v-model="editOptionC" placeholder="Masukkan pilihan C"></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan D</ion-label>
            <ion-input v-model="editOptionD" placeholder="Masukkan pilihan D"></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan E</ion-label>
            <ion-input v-model="editOptionE" placeholder="Masukkan pilihan E"></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Jawaban Benar</ion-label>
            <ion-select v-model="editCorrectAnswer" placeholder="Pilih Jawaban Benar">
              <ion-select-option value="A">A</ion-select-option>
              <ion-select-option value="B">B</ion-select-option>
              <ion-select-option value="C">C</ion-select-option>
              <ion-select-option value="D">D</ion-select-option>
              <ion-select-option value="E">E</ion-select-option>
            </ion-select>
          </ion-item>
          <!-- Editor Pembahasan Saat Edit -->
          <ion-item>
            <ion-label>Pembahasan</ion-label>
          </ion-item>
          <div id="editor-edit-explanation-container"></div>
          <ion-button expand="block" @click="updateQuestion">Simpan Perubahan</ion-button>
        </ion-content>
        <br> <br>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonInput,
  IonButton,
  IonToast,
  IonList,
  IonModal,
  IonButtons,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  updateDoc,
  doc,
  deleteDoc,
} from "firebase/firestore";
import Quill from "quill";
import "quill/dist/quill.snow.css";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);

const selectedSubject = ref("");
const optionA = ref("");
const optionB = ref("");
const optionC = ref("");
const optionD = ref("");
const optionE = ref("");
const correctAnswer = ref("");
const questions = ref([]);
const isModalOpen = ref(false);
const editOptionA = ref("");
const editOptionB = ref("");
const editOptionC = ref("");
const editOptionD = ref("");
const editOptionE = ref("");
const editCorrectAnswer = ref("");
const editQuestionId = ref("");
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
let quill;
let quillExplanation;
let quillEdit;
let quillEditExplanation;

const fetchQuestions = async () => {
  const querySnapshot = await getDocs(collection(db, "soalkedinasan"));
  questions.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
  console.log(questions.value);
  console.log('xxx');
  questions.value.forEach((question, index) => {
      console.log(`Question ${index + 1}:`, question);
  });
};

const addQuestion = async () => {
  if (!optionA.value || !optionB.value || !optionC.value || !optionD.value || !optionE.value || !correctAnswer.value) {
    showToast("Semua kolom harus diisi", "danger");
    return;
  }

  const questionText = quill.root.innerHTML;
  const explanationText = quillExplanation.root.innerHTML;

  if (!questionText) {
    showToast("Soal tidak boleh kosong", "danger");
    return;
  }

  try {
    await addDoc(collection(db, "soalkedinasan"), {
      subject: selectedSubject.value,
      question: questionText,
      options: { A: optionA.value, B: optionB.value, C: optionC.value, D: optionD.value, E: optionE.value },
      correctAnswer: correctAnswer.value,
      explanation: explanationText,
    });
    showToast("Soal berhasil ditambahkan", "success");
    resetForm();
    fetchQuestions();
  } catch (error) {
    console.error("Error adding question:", error);
    showToast("Terjadi kesalahan saat menambahkan soal", "danger");
  }
};

const editQuestion = (question) => {
  editQuestionId.value = question.id;
  editOptionA.value = question.options.A;
  editOptionB.value = question.options.B;
  editOptionC.value = question.options.C;
  editOptionD.value = question.options.D;
  editOptionE.value = question.options.E;
  editCorrectAnswer.value = question.correctAnswer;
  isModalOpen.value = true;

  setTimeout(() => {
    quillEdit = new Quill("#editor-edit-container", {
      theme: "snow",
      modules: {
        toolbar: [
          [{ header: [1, 2, false] }],
          ["bold", "italic", "underline"],
          ["image", "video"],
          [{ list: "ordered" }, { list: "bullet" }],
        ],
      },
    });
    quillEditExplanation = new Quill("#editor-edit-explanation-container", {
      theme: "snow",
      modules: {
        toolbar: [
          [{ header: [1, 2, false] }],
          ["bold", "italic", "underline"],
          ["image", "video"],
          [{ list: "ordered" }, { list: "bullet" }],
        ],
      },
    });
    quillEdit.root.innerHTML = question.question;
    quillEditExplanation.root.innerHTML = question.explanation || "";
  }, 300);
};

const updateQuestion = async () => {
  if (!editOptionA.value.trim() || !editOptionB.value.trim() || !editOptionC.value.trim() || !editOptionD.value.trim() || !editOptionE.value.trim() || !editCorrectAnswer.value.trim()) {
    showToast("Semua kolom harus diisi", "danger");
    return;
  }

  const updatedContent = quillEdit.root.innerHTML.trim();
  const updatedExplanation = quillEditExplanation.root.innerHTML.trim();

  if (!updatedContent || updatedContent === "<p><br></p>") {
    showToast("Soal tidak boleh kosong", "danger");
    return;
  }

  try {
    await updateDoc(doc(db, "soalkedinasan", editQuestionId.value), {
      question: updatedContent,
      options: { A: editOptionA.value.trim(), B: editOptionB.value.trim(), C: editOptionC.value.trim(), D: editOptionD.value.trim(), E: editOptionE.value.trim() },
      correctAnswer: editCorrectAnswer.value.trim(),
      explanation: updatedExplanation,
    });
    showToast("Soal berhasil diperbarui", "success");
    isModalOpen.value = false;
    fetchQuestions();
  } catch (error) {
    console.error("Error updating question:", error);
    showToast("Terjadi kesalahan saat memperbarui soal", "danger");
  }
};

const deleteQuestion = async (questionId) => {
  try {
    await deleteDoc(doc(db, "soalkedinasan", questionId));
    showToast("Soal berhasil dihapus", "success");
    fetchQuestions();
  } catch (error) {
    console.error("Error deleting question:", error);
    showToast("Terjadi kesalahan saat menghapus soal", "danger");
  }
};

const resetForm = () => {
  selectedSubject.value = "";
  quill.root.innerHTML = "";
  quillExplanation.root.innerHTML = "";
  optionA.value = "";
  optionB.value = "";
  optionC.value = "";
  optionD.value = "";
  optionE.value = "";
  correctAnswer.value = "";
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchQuestions();
    event.target.complete();
  }, 2000);
};

const closeModal = () => {
  isModalOpen.value = false;
};

onMounted(() => {
  quill = new Quill("#editor-container", {
    theme: "snow",
    modules: {
      toolbar: [
        [{ header: [1, 2, false] }],
        ["bold", "italic", "underline"],
        ["image", "video"],
        [{ list: "ordered" }, { list: "bullet" }],
      ],
    },
  });

  quillExplanation = new Quill("#editor-explanation-container", {
    theme: "snow",
    modules: {
      toolbar: [
        [{ header: [1, 2, false] }],
        ["bold", "italic", "underline"],
        ["image", "video"],
        [{ list: "ordered" }, { list: "bullet" }],
      ],
    },
  });

  fetchQuestions();
});
</script>

<style>
.table-container {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
#editor-container,
#editor-edit-container,
#editor-explanation-container,
#editor-edit-explanation-container {
  height: 200px;
}

.custom-modal {
  --width: 90%; /* Mengatur lebar modal */
  --height: 120%; /* Mengatur tinggi modal */
  --max-width: 800px; /* Lebar maksimum */
  --max-height: 900px; /* Tinggi maksimum */
}
</style>
